
.blog-top{
  background: linear-gradient(to bottom, rgb(255, 255, 255),rgb(122, 187, 154), rgb(255, 255, 255));
  background-size: contain;
  min-height: 530vh;
  max-width: 100vw;
}
.blog-slider img{
  border-radius: 10px;
}
.blog-image img{
   width: 152%;
   border-radius: 5px;
   padding-left: 7px;
   padding-top: 4px;
}

.blog-image img:hover{
  opacity: .9;
}

.blog-caption{

  position: relative;
  padding-left: 175px;
  margin-top: -2%;
  max-width: 300px;
  text-align:end;
  height: fit-content;
}

.blog-caption h3{
  font-weight: lighter;
  color: rgb(17, 6, 6);
}

.link-readmore{
 text-decoration:none ;
 color:rgb(32, 71, 46);
 font-size:15px;
}

.link-readmore:hover{
  font-weight: bold;
  color: rgb(47, 58, 110);
}

.blog-top hr{
  background: linear-gradient(to right, rgb(203, 224, 203),rgb(255, 255, 255),rgb(195, 219, 185), rgb(183, 223, 196));
  height: 1px;
  border-style: hidden;
}
.main{
  border-style: double;
  border-style:hidden;
  position: absolute;
  left:4%;
  top:13%;
  max-width: 60%;
  font-size:80%;
  text-align:left;
  color: rgb(4, 22, 10);
  font-family:monospace;
  font-weight:bolder;

}

.blogdata{
  
  display: flex;
  margin-top: 100px;
  padding-right: 10px;

}
.blog-header{
  position: relative;
  top: 70px;
  left: 1%;
}
.blog-header h2{
 
  color: rgb(32, 71, 46);
  font-weight: lighter;
  font-size: 30px;
}
.blog-header h2:hover{
  opacity: .9;
  color: rgb(23, 80, 23);
}
.blog-header p1{
  float: left;
  color: rgb(9, 41, 23);
  font-weight: lighter;
  padding-left: 5px;
  margin-top: 2px;
}
.blog{
  width: 65vw;
  border-radius: 20px;
}

.blog-slider{

  width: 49%;
  margin-left: 50%;
  padding-top: 5%;
}

   .top{
    padding-left: 12vw;
    padding-top: 90px;
    margin-bottom: 0px;
    background-color:inherit;   
   }
  
   .top input{
    width: 110px;
    background-color: inherit;
    ;
    border-style:inherit;
}
.avatar-blog{ 
  float: left;
  margin-top:75px;
  margin-left:4vw;

}
.top button{
  width: 30px;
  height: 37px;
  border-style: hidden;
  background-color: inherit;
  cursor: pointer;
}
.top button:hover{
  color: rgb(29, 87, 29);
}
.top-ul{
  display: flex;
  margin-left: 45vw;
  width: 48vw;

}
.top-ul> li{
  list-style: none;
  padding-left: 30px;
}
.top-ul a{
  text-decoration: none;
  color: rgb(44, 21, 21);
  font-weight: lighter;
  font-size: 18px;
  
  font-weight: lighter;

}
.top-ul a:hover{
  color: rgb(16, 17, 63);
}
  

  .RandomQoutes{
    border-left-style: outset;
    border-width: thin;
    border-left-color: rgb(180, 228, 174);
    height: 110vh;
    padding-left: 40px;
    opacity: 0.8;
    position: absolute;
    text-shadow: 3px 3px 3px rgb(187, 233, 187);
    top:350px;
    right: 50px;
    text-align:center;
    font-style: oblique;
    font-size: 115%;
    color: rgb(5, 20, 3);
    font-weight: lighter;
  
  }

  .pagination {
    display: flex;
    justify-content: space-evenly;
    margin-top: 7%;
    margin-left: 70%;
  }
  
  .pagination a {
    color: black;
    float: left;
    font-size: 16px;
    text-decoration: none;
  }
  .carousel-blog{
    padding-top:2%;
    margin-left: 16%;
    border-top-left-radius: 1%;
    border-top-right-radius: 1%;
    max-height: 230px;
    width: 70%;
    background: linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255),rgb(240, 240, 240), rgb(255, 255, 255), rgb(255, 255, 255));
}
.carousel-blog img{
   max-width: 85%;
}

  

  @media screen and (max-width: 800px){
    .blog-container{
      min-height: fit-content;
    }
    .blog-top{
      min-height: 300vh;
    }
    .avatar-blog{ 
      display: none;
    }
    .RandomQoutes{
      display: none;
    }
    .carousel-blog{
      margin-top:16%;
      margin-left: -41%;
      max-height: 230px;
      width: 100%;
      display: none;
    }
    .top{
      padding-left: 22vw;
      padding-top: 25px;
      min-width: 49vw;
      margin-bottom: 0px;
      background-color:inherit; 

     }
     .top-ul{
       margin-top: 0px;
       flex-direction: column;     
       margin-left:22vw;
    }
    .top-ul> li{
      text-align: center;
      list-style: none;
      padding-left: 11px;
      padding:5px;
    }
    .top-ul a{
      color: rgb(54, 54, 54);
      font-weight: lighter;
      font-size: 13px;
      font-weight: lighter;
      padding-top: 20px;
    
    }



    .blog-header{
      top: 20px;
      left: 3%;   
    }
    .blog-header h2{
     
      font-size: 16px;
      min-width: 320px;
    }

    .blog-header p1{
      color: rgb(15, 49, 37);
      padding-left: 1%;
      margin-top: 3px;
    }

    .blog-image img{
      width: 2350%;
      height: 130px;
   }
   .blog-caption{
     padding-left: 173px;
     margin-top: -17%;
     min-width: 150px;
     text-align:end;
     max-height: 199px;
     overflow:hidden;

   }
   .blog-caption h3{
     min-width: 136px;
     max-width: 136px;
     font-size: 11px;
     color: rgb(7, 17, 11);
   }
   .blogdata{
     
     display: flex;
     margin-top: 90px;
     padding-right: 10px;
   }



  }
