.home{
    background-image: url('../Img/5.jpg');
    height: 100vh;
    width: 100%;
    background-size: cover;

    animation: bounceInLeft;
    animation-duration: 1.5s;
}

.home-hr{
    position: relative;
    top:2vh;
    overflow-x: hidden;
    margin-left: 0%;
}
.home hr{
    background: linear-gradient(to right, rgb(241, 241, 241),rgb(197, 202, 202), rgb(243, 243, 243));
    height: 1px;
    border-style: hidden;
  }
.home-center{
    text-align: center;
}

.home-data{
    float: left;
    margin-top: 17.5%;
    margin-left: 40%;
    font-family:sans-serif;
    border-radius: 20%;
    max-height: 10vh;
}
.home-data p{
    color: rgb(17, 36, 2);
    font-family:Roboto;
  
}
.carousel{
    padding-top:2%;
    margin-left: 16%;
    border-top-left-radius: 1%;
    border-top-right-radius: 1%;
    max-height: 230px;
    width: 70%;
    background: linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255),rgb(240, 240, 240), rgb(255, 255, 255), rgb(255, 255, 255));

    

}
.carousel img{
   max-width: 85%;
}




.home-center{
    height: 90vh;
    position: relative;
    width: 100%;
    animation: bounceInDown;
    animation-duration: 1.5s;
    
}


.stylediv{
    background: white;
    color: black;
    position: relative;
    bottom: 1vh;
    height: min-content;
    text-align: center;
    width: 100%;  
}

.stylediv::before{
 position: absolute;
 content: '';
 width: 100%;
 height: 80px;
 top: 20px;
 padding-bottom: 170px;
 left: 0;
 bottom: 0;
 background:white;

 transform: skewY(10deg);
}
.stylediv::after{
    position: absolute;
    content: '';
    width: 100%;
    height: 80px;
    top: 20px;
    padding-bottom: 170px;
    right: 0;
    background: white;
    transform: skewY(-10deg);
   }

   
.home-others{
  padding-top: 1.1%;
  max-width: 100%;
  min-height: 120vh;

}

.video{
  position: relative;
  display: flex;
  margin-top: 1%;
  padding-left:10%;
  
}
.video p{
  color: rgb(18, 36, 36);
  padding-left: 0px;
  padding-top: 0px;
  max-width: 700px;
  text-align: center;
}
.video h2{
  color: rgb(18, 31, 24);
  text-align: left;
  margin-left: 20px;
}
.video a{
  text-decoration: none;
}
.video p:hover {
  
  
  color: #0e4e24;
}
.slider{
  border-right-style: inset;
  border-width: thin;
  margin-left: 5px;
  padding-right: 7px;
  max-width: fit-content;
  max-height: fit-content;
  background-color:rgb(243, 243, 243) ;
  border-radius: 1%;
}
.slider a{
  text-decoration: none;
  color: rgb(3, 15, 2);
}

.slider-header h2{
  text-align: center;
  font-weight: lighter;
  font-family: inherit;
  font-size: 12px;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-right: 0px;
  background-color: inherit;
  font-weight: bold;
}
.slider-image img{
  width: 100%;
  border-radius: 10px;
  padding-left: 10%;
}
.slider-caption p{
  padding-bottom: 10px;
  text-align: center;
  margin-left: 33px;
  max-width: 230px;
  font-size: 10px;
}
.home-im{
  position: relative;
  top:0vh;
  width: 100%;
  background-image: url("../Img/73.jpg");
  height: 50vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode:soft-light;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  border-top-left-radius: 0%;
  border-top-right-radius: 0%;
}
   

video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;

}



.infoanim{
  animation: mymove 8s infinite;
  -moz-animation: mymove 8s infinite;
  -webkit-animation: mymove 8s infinite;
  -o-animation: mymove 8s infinite;    
  color: rgb(17, 36, 2);
  font-family:'Trebuchet MS';
  position: absolute;
  margin-left: 20%;
  margin-top: 15%;
 }
 .opacityanim{
  animation: mymove1 3s infinite;
  -moz-animation: mymove1 3s infinite;
  -webkit-animation: mymove1 3s infinite;
  -o-animation: mymove1 3s infinite;    
  color: rgb(17, 36, 2);
  font-family:'Trebuchet MS';
 }


   @keyframes mymove {
    0%   { opacity:0; }
    50%  { opacity:1; }
    100% { opacity:0; }
    from {right: 220px ;}
    to {right:500px ;}
  }
   @-moz-keyframes mymove {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {padding-left: 340px ;}
    to {padding-left:0 ;}
  }
   @-webkit-keyframes mymove {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {padding-left: 340px ;}
    to {padding-left:0 ;}
  }
   @-o-keyframes mymove {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {padding-left: 340px ;}
    to {padding-left:0 ;}
  }



  @keyframes mymove1 {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
  @-moz-keyframes mymove1 {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
   @-webkit-keyframes mymove1 {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
   @-o-keyframes mymove1 {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }









