.nav{  
    background: linear-gradient(to bottom, rgb(255, 255, 255),rgb(255, 255, 255), rgb(255, 255, 255));
    height: 50px;
    width: 100%;
    padding-top: 25px;
    position: fixed;
    z-index: 1;
}
.nav-black{
  position: fixed;
  width: 100vw;
  background-color: rgb(255, 255, 255);
  height: 25px;
  padding-top: 5px;
  padding-bottom: 3px;
  z-index: 1;
}
.nav-black> .nav-ul a{
  color: rgb(0, 0, 0);
  font-size: 14px;
}
.nav-black> .dropdown> .dropbtn{
  color: rgb(0, 0, 0);
}


.nav-ul{
    display: flex;
    margin-left: 50vw;
}

.nav-ul> li{
    list-style-type: none;
    padding-right: 30px;
}
.nav-ul a:hover{
   color: rgb(29, 99, 27);
}
.nav-ul a{
  text-decoration: none;
  color: black;
  font-weight: lighter;
}
.dropdown-nav{
    float: left;
    margin-left: 20%;

}






















.dropdown {
    float: left;
    margin-left: 19.5%;
    overflow: hidden;
    z-index: 100;
    font-weight: lighter;
  }
  
  .dropdown .dropbtn {
    font-size: inherit;
    border: none;
    outline: none;
    background-color: inherit;
    z-index: 100;
    color: rgb(17, 6, 6);
    font-family: inherit;
    font-weight: lighter;
  }

  
  .dropdown-content {
    font-weight: lighter;
      display: none;
      position: absolute;
      left: 17%;
      font-size: 12px;
      max-width: 174px;
      background-color: rgb(255, 255, 255) ;
      z-index: 100;
  }
  
  .dropdown-content a {
    
    font-family: sans-serif;
    font-weight: lighter;
    float: right;
    text-decoration: none;
    font-family: cursive;
    text-align: center;
    color: rgb(17, 6, 6);
    font-size: 10px;
    padding: 10px 17px;
    display: block;
    width: 140px;
    z-index: 100;
  }

  .dropdown-content a:hover {

    background-color:  rgb(98, 156, 146);
    color: white;
    z-index: 100;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  .dropdown:hover .dropbtn {
    background-color: inherit;
    color: rgb(33, 82, 55);
  }

  .mobile-menu{
    position: absolute;
    right: 2%;
    top:90%;
    text-align: right;
    display: none;

  }
  .mobile-menu a{
  
    color: black;
  }


  @media screen and (max-width: 800px) {
    .nav{  
      height: 25px;
      padding-bottom: 15px;
  }
  .nav-black{
    width: 100vw;
    height: 25px;
    padding-bottom: 15px;

  }

  .nav-ul{
    display: none;
}
.dropbtn{
  display: none;
}
.dropdown-content{
  display: none;
}
.mobile-menu {
  display: block;
  background-color: rgb(255, 255, 255);
  padding-bottom: 20px;
  padding-right: 15px;
  border-bottom-left-radius: 30px;
}
.hide-menu{
  display: none;
}
.mob-ul> li{
  font-size: 13px;
  padding: 10px;
  width: 100vw;
}
.menu-icon{
  position: absolute;
  right: 5%;
  top: 30%;
  cursor: pointer;
}

.nav-ul> li{
    list-style-type: none;
    padding-right: 0px;
}
.nav-ul a:hover{
   color: rgb(29, 99, 27);
}
.nav-ul a{
  text-decoration: none;
  color: black;
  font-weight: lighter;
}
.dropdown-content a{
  display: none;
}
  }
